.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 33px 0;
  background-color: var(--light-color);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.25);

  &__container {
    @include flex-all-sb;
  }
}

.logo {
  @include tablet {
    max-width: 100px;
  }
}
